import React from "react";
import { Link } from "react-router-dom";
import './navbar.css'

function navbar() {
  return (
    <nav className="navbar navbar-expand-lg nav-header">
      <div className="container nav-content">
          <Link to="/" className="logo">
            <h1>
              <i className="fas fa-shopping-bag"></i> Home
            </h1>
          </Link>
        <button
          className="navbar-toggler"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#navbarSupportedContent"
          aria-controls="navbarSupportedContent"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav align-items-center ms-auto mb-2 mb-lg-0">
            <li className="nav-item">
              <Link className="nav-link " to="/products">
                Products
              </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" aria-current="page" to="/counter">
                Counter App
              </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" aria-current="page" to="/todo">
                Todo App
              </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" aria-current="page" to="/login">
                Login
              </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" aria-current="page" to="/register">
                Register
              </Link>
            </li>
          </ul>
        </div>
      </div>
    </nav>
  );
}

export default navbar;
